import React from "react";
import './Pagination.css';
function Pagination({breedsPerPage, totalBreeds, Paginate, currentPage}){
    const pageNumbers = []
    for(let i = 1; i <= Math.ceil(totalBreeds/breedsPerPage);i++){
        pageNumbers.push(i)
    }
    return (
        <section className="section-pagination">
            <article>
                <ul className="pagination">
                    {
                        pageNumbers.map(number => (
                            <li key ={number} >
                                <a className={currentPage === number ? 'active' : ''} href="#" onClick={Paginate}>
                                    {number}
                                </a>
                            </li>
                        ))
                    }
                </ul>
            </article>
        </section>
    )
}

export default Pagination