import React, { useState } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import NavBar from "./components/NavBar.jsx";
import Detail from "./components/Detail.jsx";
import BreedList from "./components/BreedList.jsx";
import AddBreed from "./components/AddBreed.jsx";
import SuccessPage from "./components/SuccessPage.jsx";
import Footer from "./components/Footer.jsx";
import './App.css'; 
import WelcomePage from "./components/WelcomePage.jsx";

function App() {
  const [welcome, setWelcome] = useState(true)
  const handleWelcome = ()=> {
    setWelcome(false)
  }
  return (
    <Router>
        {welcome ? (
            <WelcomePage handleWelcome={handleWelcome} />
            ):(
              <>
                <NavBar />
                <div >
                  <Switch>
                    <Route exact path="/doglist" component={BreedList} />
                    <Route exact path="/add" component={AddBreed} />
                    <Route path="/detail/:id" component={Detail} />
                    <Route path="/success" component={SuccessPage} />
                  </Switch>
                  <Footer />
                </div>
              </>
            )}
        
        
    </Router>
  );
}

export default App;
