import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  getAllBreeds,
  getBreedSort,
  getBreedByName
} from "../actions/breeds";
import BreedCard from "./BreedCard";
import './BreedList.css';
import FullPageLoader from "./FullPageLoader";
import Pagination from "./Pagination";

function BreedList(){   
    const [inputName, setInputName] = useState('');
    const [selectOrder, setselectOrder] = useState({
        key :'',
        direction : ''
    });
    const [currentPage, setCurrentPage] = useState(1);
    const [breedsPerPage] = useState(8);


    const breeds = useSelector(state => state.data);
    const totalBreeds = useSelector(state => state.totalItems);

    const dispatch = useDispatch();

    useEffect(() => {
        if(!breeds.length) dispatch(getAllBreeds());
      }, []);
    

    const handleSubmit = (e) => {
        e.preventDefault()
        
        dispatch(getBreedByName(inputName.toLowerCase()))
        setCurrentPage(1)
        setselectOrder({
            key :'',
            direction : ''
        })
    }

    const handleInputChange = (e) => {
        setInputName(e.target.value);
    }

    const handleSelectChange = (e) =>{
        let [key, direction] = e.target.value.split('_');
        setselectOrder({
            key,
            direction
        })
        dispatch(getBreedSort({direction, key, name: inputName, page:currentPage}))
    }

    const Paginate = (e) => {
        e.preventDefault()
        setCurrentPage(Number(e.target.innerText))
        
        dispatch(getBreedSort({
            direction: selectOrder.direction, 
            key:selectOrder.key, 
            name: inputName,
            page:Number(e.target.innerText)
        }))
    }

    return (
        <main className="container">
            <section className="section-finder">
                <form className="form-finder" onSubmit={handleSubmit}>
                    <div>
                        <input name={inputName} onChange={handleInputChange} className="input-finder"/>
                        <button className="button-contact">SEND</button>
                    </div>
                    
                    <div className="filter-section">
                        <select onChange={handleSelectChange}  className="select-finder">
                            <option value="name_asc" selected={ !selectOrder.key && 'selected'} > Name Ascending </option>
                            <option value="name_desc"> Name Descending </option>
                            <option value="weight_asc"> Weight Ascending </option>
                            <option value="weight_desc"> Weight Descending </option>
                        </select >
                    </div>
                </form>
            </section>
            <section className="card-row">
                {
                    breeds.map((breed,index)=>(
                        <BreedCard
                            id={breed.id}
                            image={breed.image}
                            name={breed.name}
                            temperament={breed.temperament}
                        />
                    )
                )}
            </section>
            <Pagination
               breedsPerPage={breedsPerPage}
               totalBreeds = {totalBreeds}
               Paginate = {Paginate}
               currentPage = {currentPage}
            />
            <FullPageLoader />
        </main>
    )
}

export default BreedList